import React, { useState } from "react";

export default function TextForm(props) {
  //text = "New text"; //Incorrect way of setting the value of the state;
  //setText("New text"); //Correct way of setting the value of the state;

  const HandleUpClick = () => {
    let new_text = text.toUpperCase();
    setText(new_text);
    props.showAlert("Converted to Uppercase!", "success");
  };

  const HandleDownClick = () => {
    let new_text = text.toLowerCase();
    setText(new_text);
    props.showAlert("Converted to Lowercase!", "success");
  };
  const ClearClick = () => {
    let new_text = "";
    setText(new_text);
    props.showAlert("Cleared the Input!", "success");
  };

  //Remove extra spaces using regex
  const HandleExtraSpaces = () => {
    let new_text = text.split(/[ ]+/);
    setText(new_text.join(" "));
    props.showAlert("Removed additional spacing!", "success");
  };

  //Select & copy the text
  const HandleCopyText = () => {
    navigator.clipboard.writeText(text);
    props.showAlert("Copied text to the clipboard!", "success");
  };

  const HandleOnChange = (event) => {
    setText(event.target.value);
  };

  const [text, setText] = useState("Enter text here!");

  return (
    <div
      style={{
        color: props.Mode === "light" ? "black" : "white",
      }}
    >
      <div>
        <h1>{props.heading}</h1>
        <div className="mb-3">
          <label htmlFor="myBox" className="form-label">
            Example textarea
          </label>
          <textarea
            className="form-control"
            id="myBox"
            rows="8"
            value={text}
            onChange={HandleOnChange}
            style={{
              backgroundColor: props.Mode === "light" ? "white" : "#13466e",
              color: props.Mode === "light" ? "black" : "white",
            }}
          ></textarea>
        </div>
        <button
          disabled={text.length === 0}
          className="btn btn-primary mx-1 my-1"
          onClick={HandleUpClick}
        >
          Convert to Uppercase
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary mx-1 my-1"
          onClick={HandleDownClick}
        >
          Convert to Lowercase
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary mx-1 my-1"
          onClick={HandleCopyText}
        >
          Copy Text
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-primary mx-1 my-1"
          onClick={HandleExtraSpaces}
        >
          Remove Extra Spaces
        </button>
        <button
          disabled={text.length === 0}
          className="btn btn-danger mx-1 my-1"
          onClick={ClearClick}
        >
          Clear
        </button>
      </div>
      <div className="container my-3">
        <h2>Summary</h2>
        <p>
          {text.length > 0
            ? text.split(/\s+/).filter((element) => {
                return element.length !== 0;
              }).length +
              " words & " +
              text.length +
              " characters."
            : "0 word & 0 character."}
        </p>
        <p>
          {text.length > 0
            ? "It will take " +
              Math.round(
                0.8 *
                  text.split(" ").filter((element) => {
                    return element.length !== 0;
                  }).length
              ) +
              " Minutes to read."
            : "It will take 0 Minute to read."}
          {/* It will take {Math.round(0.8 * text.split(" ").length)} Minutes to
          read. */}
        </p>
        {/* if (text.split(" ").length == 1)
        {
            <p>It will take {0.8 * text.split(" ").length} Minute to read.</p>
        }
        else if (text.split(" ").length > 1)
        {
            <p>It will take {0.8 * text.split(" ").length} Minutes to read.</p>
        }
        else
        {
            <p>It will take 0 Minute to read.</p>
        }
        <h2>Prview</h2> */}
        <p>{text.length > 0 ? text : "Nothing to preview!"}</p>
      </div>
    </div>
  );
}
