import PropTypes from "prop-types";
import React from "react";
import { Link } from "react-router-dom";

export default function Navbar(props) {
  return (
    <>
      <nav
        className={`navbar navbar-expand-lg navbar-${props.Mode} bg-${props.Mode}`}
      >
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">
            {props.title}
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link" aria-current="page" to="/">
                  {props.hometext}
                </Link>
                {/* <a
                  className="nav-link active"
                  aria-current="page"
                  href="#"
                >
                  {props.hometext}
                </a> */}
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/About">
                  {props.abouttext}
                </Link>
              </li>
            </ul>
            <form className="d-flex" role="search">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button className="btn btn-primary" type="submit">
                {props.searchbtntext}
              </button>
            </form>

            <div className="d-flex mx-3">
              <div
                className="bg-primary rounded mx-2 shadow p-1"
                style={{ height: "30px", width: "30px", cursor: "pointer" }}
                // onClick="colorChange"
                onClick={() => {
                  props.toggleMode("primary");
                }}
              ></div>
              <div
                className="bg-danger rounded mx-2 shadow p-1"
                style={{ height: "30px", width: "30px", cursor: "pointer" }}
                // onClick="colorChange"
                onClick={() => {
                  props.toggleMode("danger");
                }}
              ></div>
              <div
                className="bg-success rounded mx-2 shadow p-1"
                style={{ height: "30px", width: "30px", cursor: "pointer" }}
                // onClick="colorChange"
                onClick={() => {
                  props.toggleMode("success");
                }}
              ></div>
              <div
                className="bg-warning rounded mx-2 shadow p-1"
                style={{ height: "30px", width: "30px", cursor: "pointer" }}
                // onClick="colorChange"
                onClick={() => {
                  props.toggleMode("warning");
                }}
              ></div>
              <div
                className="bg-white rounded mx-2 shadow p-1"
                style={{ height: "30px", width: "30px", cursor: "pointer" }}
                // onClick="colorChange"
                onClick={() => {
                  props.toggleMode("light");
                }}
              ></div>
              <div
                className="bg-dark rounded mx-2 shadow p-1"
                style={{ height: "30px", width: "30px", cursor: "pointer" }}
                // onClick="colorChange"
                onClick={() => {
                  props.toggleMode("dark");
                }}
              ></div>
            </div>

            <div
              className={`form-check form-switch mx-2 my-2 text-${
                props.Mode === "light" ? "black" : "white"
              }`}
            >
              <input
                className="form-check-input"
                type="checkbox"
                //role="switch"
                id="flexSwitchCheckDefault"
                onClick={() => {
                  props.toggleMode("null");
                }}
              />
              <label
                className="form-check-label"
                htmlFor="flexSwitchCheckDefault"
              >
                Enable Dark mode
              </label>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
}

Navbar.propTypes = {
  //Here the "P" of propTypes is always small "p" and the body of this will have the capital "P"
  title: PropTypes.string.isRequired,
  hometext: PropTypes.string.isRequired,
  abouttext: PropTypes.string.isRequired,
};

Navbar.defaultProps = {
  //Here the "P" of propTypes is always small "p" and the body of this will have the capital "P"
  title: "Todo List",
  hometext: "Home",
  abouttext: "About Us",
};
